  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2);
  max-width: 440px;
  width: 100%;
  background-color: white;
  box-sizing: border-box;
  z-index: 10; }

.aside__feedback.active,
.aside__callback.active {
  transform: translateX(0); }

.aside__button {
  padding-top: 24px;
  padding-left: 16px;
  margin-bottom: 40px; }

.title__feedback,
.title__callback {
  position: relative;
  margin-bottom: 24px; }

.title__feedback::before,
.title__callback::before {
  content: '';
  position: absolute;
  left: 0;
  border-right: 4px solid #41F6D7;
  height: 40px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px; }

.input__wrapper {
