.accept {
  font-size: 14px;
  margin: 16px; }

.accept__document {
  display: inline;
  padding-left: 0;
  color: #ff3e79;
  transition: opacity 0.3s ease; }

.accept__document:hover {
  opacity: 0.5; }

.accept__document:active {
  opacity: 0.5; }

.feedback__button,
.callback__button {
  position: absolute;
  right: 16px; }

.feedback__overlay,
.callback__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
